textarea {
   
    height: 80px;
    padding: 12px 20px;
    box-sizing: border-box;
    width: 100%;
    background-color: transparent;
    border: 0px solid;
    resize: none;
    font-size: 15px;
  }

  input[type=text] {
    width: 100%;
    padding: 12px 20px;
    background-color: transparent;
    border: 0px solid;
    box-sizing: border-box;
    font-size: 20px;
  }
  
  .notemain {
    width: 300px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    margin-left:5px;
    margin-top:10px;
    
  }
  textarea:focus, input:focus{
    outline: none;
}